import React from "react";
import {
  SafeAreaView,
  View,
  Image,
  Text,
  TouchableOpacity,
} from "react-native";
import { styles } from "./style";

export default function Timing({ navigation }) {
  return (
    <SafeAreaView style={{ flex: 1 }}>
      <View
        style={{ height: 100, flexDirection: "row", backgroundColor: "black" }}
      >
        <View style={{ flex: 1, marginLeft: "5%" }}>
          <Image
            style={{ width: 100, height: 100, resizeMode: "stretch" }}
            source={{
              uri: "https://avatars.mds.yandex.net/i?id=1cf6c87ac65c652a876458b3ec1808f8bcee4d07-8370529-images-thumbs&n=13",
            }}
          />
        </View>
        <View
          style={{ flex: 5, backgroundColor: "black", flexDirection: "row" }}
        >
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Main");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.text}>Главная</Text>
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("News");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.text}>Новости</Text>
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Timing");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.active}>Расписание</Text>
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Contacts");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.text}>Контакты</Text>
          </TouchableOpacity>
        </View>
      </View>
      <View
        style={{
          flex: 1,
          background: "linear-gradient(to bottom, #450045, black)",
          justifyContent: "center",
        }}
      >
        <Text
          style={{
            textAlign: "center",
            color: "white",
            fontSize: 40,
            fontFamily: "bold",
          }}
        >
          {
            "Расписание занятий:\n\nПонедельник: 18:00 - 20:00\n\nВторник: 18:00 - 20:00\n\nСреда: 18:00 - 20:00\n\nЧетверг: 18:00 - 20:00\n\nПятница: 18:00 - 20:00\n\nСуббота: Выходной\n\nВоскресенье: Выходной\n"
          }
        </Text>
      </View>
    </SafeAreaView>
  );
}
