import React from "react";
import {
  SafeAreaView,
  View,
  Image,
  Text,
  TouchableOpacity,
  FlatList,
  ScrollView,
} from "react-native";
import { styles } from "./style";

export default function News({ navigation }) {
  const data = [
    {
      id: 1,
      title: "Lorem Ipsum Dolor Sit Amet",
      description:
        "Consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.",
      mainText:
        "Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.",
      image:
        "https://avatars.mds.yandex.net/i?id=e44fdac8e1011eeef600ffadb90d4c9c80064eb0-10919901-images-thumbs&n=13",
    },
    {
      id: 2,
      title: "Sed Ut Perspiciatis",
      description:
        "Nemo enim ipsam voluptatem quia voluptas sit aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos qui ratione.",
      mainText:
        "Voluptatem sequi nesciunt, neque porro quisquam est, qui dolorem ipsum quia dolor sit amet, consectetur, adipisci velit, sed quia non numquam eius modi tempora incidunt ut labore et dolore magnam aliquam quaerat voluptatem.",
      image:
        "https://avatars.mds.yandex.net/i?id=70ef60f7e8551e5d4bb97c1972658aa50fb6c207-10918904-images-thumbs&n=13",
    },
    {
      id: 3,
      title: "At Vero Eos Et Accusamus",
      description:
        "Et iusto odio dignissimos ducimus qui blanditiis praesentium voluptatum deleniti atque corrupti quos dolores et quas molestias excepturi.",
      mainText:
        "Sint occaecati cupiditate non provident, similique sunt in culpa qui officia deserunt mollitia animi, id est laborum et dolorum fuga. Et harum quidem rerum facilis est et expedita distinctio.",
      image:
        "https://avatars.mds.yandex.net/i?id=b46e1bd2b086b320305520cb3291be46-5231808-images-thumbs&n=13",
    },
    {
      id: 4,
      title: "On the other hand, we denounce",
      description:
        "With righteous indignation and dislike men who are so beguiled and demoralized by the charms of pleasure of the moment, so blinded by desire.",
      mainText:
        "That they cannot foresee the pain and trouble that are bound to ensue; and equal blame belongs to those who fail in their duty through weakness of will, which is the same as saying through shrinking from toil and pain.",
      image:
        "https://avatars.mds.yandex.net/i?id=be16b37f241d646293ef078a050f8588c701f6cd-5280120-images-thumbs&n=13",
    },
  ];
  return (
    <SafeAreaView style={{ flex: 1 }}>
      <View
        style={{ height: 100, flexDirection: "row", backgroundColor: "black" }}
      >
        <View style={{ flex: 1, marginLeft: "5%" }}>
          <Image
            style={{ width: 100, height: 100, resizeMode: "stretch" }}
            source={{
              uri: "https://avatars.mds.yandex.net/i?id=1cf6c87ac65c652a876458b3ec1808f8bcee4d07-8370529-images-thumbs&n=13",
            }}
          />
        </View>
        <View
          style={{ flex: 5, backgroundColor: "black", flexDirection: "row" }}
        >
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Main");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.text}>Главная</Text>
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("News");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.active}>Новости</Text>
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Timing");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.text}>Расписание</Text>
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Contacts");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.text}>Контакты</Text>
          </TouchableOpacity>
        </View>
      </View>
      <View
        style={{
          flex: 1,
          background: "linear-gradient(to bottom, #002e19, black)",
        }}
      >
        <FlatList
          data={data}
          numColumns={1}
          style={{ margin: 5 }}
          renderItem={({ item, index }) => {
            return (
              <TouchableOpacity
                onPress={() => {
                  navigation.navigate("InfoNews", item);
                }}
                style={{ margin: 5, flexDirection: "row" }}
              >
                <Image
                  style={{
                    margin: 20,
                    width: 250,
                    height: 250,
                  }}
                  source={{ uri: item.image }}
                />
                <View>
                  <Text style={styles.InfoNews.text.title}>{item.title}</Text>

                  <Text style={styles.InfoNews.text.mainText}>
                    {item.description}
                  </Text>
                </View>
              </TouchableOpacity>
            );
          }}
        />
      </View>
    </SafeAreaView>
  );
}
