import React from "react";
import {
  SafeAreaView,
  View,
  Image,
  Text,
  TouchableOpacity,
  Linking,
  Button,
} from "react-native";
import { styles } from "./style";

export default function Contacts({ navigation }) {
  const imagePath = require("../assets/imageMap.png");
  const handleOpenLink = () => {
    const url =
      "https://yandex.ru/maps/10951/volzhskiy/?ll=44.821054%2C48.753939&mode=routes&rtext=~48.762003%2C44.821477&rtt=auto&ruri=~ymapsbm1%3A%2F%2Fgeo%3Fdata%3DCgg1Nzg1NzgzNRJw0KDQvtGB0YHQuNGPLCDQktC-0LvQs9C-0LPRgNCw0LTRgdC60LDRjyDQvtCx0LvQsNGB0YLRjCwg0JLQvtC70LbRgdC60LjQuSwg0YPQu9C40YbQsCA0MCDQu9C10YIg0J_QvtCx0LXQtNGLLCA4MCIKDTJJM0IVSgxDQg%2C%2C&z=15.33";

    Linking.openURL(url).catch((error) =>
      console.error("Ошибка при открытии ссылки:", error)
    );
  };
  return (
    <SafeAreaView style={{ flex: 1 }}>
      <View
        style={{ height: 100, flexDirection: "row", backgroundColor: "black" }}
      >
        <View style={{ flex: 1, marginLeft: "5%" }}>
          <Image
            style={{ width: 100, height: 100, resizeMode: "stretch" }}
            source={{
              uri: "https://avatars.mds.yandex.net/i?id=1cf6c87ac65c652a876458b3ec1808f8bcee4d07-8370529-images-thumbs&n=13",
            }}
          />
        </View>
        <View
          style={{ flex: 5, backgroundColor: "black", flexDirection: "row" }}
        >
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Main");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.text}>Главная</Text>
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("News");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.text}>Новости</Text>
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Timing");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.text}>Расписание</Text>
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Contacts");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.active}>Контакты</Text>
          </TouchableOpacity>
        </View>
      </View>
      <View style={{ flex: 1, background: "linear-gradient(to bottom, #003545, black)", }}>
        <View style={{ flex: 1, marginLeft: "10%", marginTop: "2%" }}>
          <TouchableOpacity
            style={{ width: "90%", height: "70%" }}
            onPress={handleOpenLink}
          >
            <Image
              style={{ width: "100%", height: "100%" }}
              source={imagePath}
            />
          </TouchableOpacity>
          <Text style={{ color: "white", fontSize: 20, marginBottom: 20 }}>
            {"Мы находимся по адресу: улица 40 лет Победы, 80"}
          </Text>
          <Text style={{ color: "white", fontSize: 20, marginBottom: 20 }}>
            {"Контактный телефон +7(996)-ХХХ-ХХ-ХХ"}
          </Text>
          <Text style={{ color: "white", fontSize: 20, marginBottom: 20 }}>
            {"Время работы с 8:00 до 16:00 с понедельника до пятницы "}
          </Text>
        </View>
      </View>
    </SafeAreaView>
  );
}
