import React from "react";

import Main from "./components/Main";
import News from "./components/News";
import Contacts from "./components/Contacts";
import Timing from "./components/Timing";
import InfoNews from "./components/InfoNews";

import { createStackNavigator } from "@react-navigation/stack";
import { NavigationContainer } from "@react-navigation/native";

const Stack = createStackNavigator();

export default function Navigation() {
  return (
    <NavigationContainer>
      <Stack.Navigator
        screenOptions={{
          headerShown: false,
        }}
      >
        <Stack.Screen
          name="Main"
          component={Main}
          options={{
            headerShown: false,
            title: "Главная",
          }}
        />
        <Stack.Screen
          name="News"
          component={News}
          options={{
            title: "Новости",
            headerStyle: {
              backgroundColor: "black",
            },
            headerTintColor: "white",
            headerTitleStyle: {
              fontWeight: "bold",
            },
          }}
        />
        <Stack.Screen
          name="Contacts"
          component={Contacts}
          options={{
            title: "Контакты",
            headerStyle: {
              backgroundColor: "black",
            },
            headerTintColor: "white",
            headerTitleStyle: {
              fontWeight: "bold",
            },
          }}
        />
        <Stack.Screen
          name="Timing"
          component={Timing}
          options={{
            title: "Расписание",
            headerStyle: {
              backgroundColor: "black",
            },
            headerTintColor: "white",
            headerTitleStyle: {
              fontWeight: "bold",
            },
          }}
        />
        <Stack.Screen
          name="InfoNews"
          component={InfoNews}
          options={{
            headerStyle: {
              backgroundColor: "black",
            },
            headerTintColor: "white",
            headerTitleStyle: {
              fontWeight: "bold",
            },
          }}
        />
      </Stack.Navigator>
    </NavigationContainer>
  );
}
