import React from "react";
import {
  SafeAreaView,
  View,
  Image,
  Text,
  TouchableOpacity,
} from "react-native";
import { styles } from "./style";

export default function Main({ navigation }) {
  const meetAstro = require("../assets/meetAstro.png");

  return (
    <SafeAreaView style={{ flex: 1 }}>
      <View
        style={{ height: 100, flexDirection: "row", backgroundColor: "black" }}
      >
        <View style={{ flex: 1, marginLeft: "5%" }}>
          <Image
            style={{ width: 100, height: 100, resizeMode: "stretch" }}
            source={{
              uri: "https://avatars.mds.yandex.net/i?id=1cf6c87ac65c652a876458b3ec1808f8bcee4d07-8370529-images-thumbs&n=13",
            }}
          />
        </View>
        <View
          style={{ flex: 5, backgroundColor: "black", flexDirection: "row" }}
        >
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Main");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.active}>Главная</Text>
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("News");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.text}>Новости</Text>
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Timing");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.text}>Расписание</Text>
          </TouchableOpacity>
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Contacts");
            }}
            style={styles.head.touchableOpacity}
          >
            <Text style={styles.head.text}>Контакты</Text>
          </TouchableOpacity>
        </View>
      </View>
      <View
        style={{
          flex: 1,
          flexDirection: "row",
        }}
      >
        <View
          style={{
            background: "linear-gradient(to bottom, #090020, black)",
            flex: 1,
          }}
        >
          <Image
            style={{
              width: 600,
              height: 600,
              resizeMode: "stretch",
              marginHorizontal: "auto",
              marginVertical: "10%",
            }}
            source={{
              uri: "https://avatars.mds.yandex.net/i?id=24241a8e4df02372a35dd04ac3c0250bf7d2ec1f-10591076-images-thumbs&n=13",
            }}
          />
          <Text
            style={{
              color: "white",
              fontSize: 30,
              fontFamily: "bold",
              textAlign: "center",
              margin: 20,
            }}
          >
            {
              "Мы занимаемся изучением космоса\nделаем научные снимки\nи просто комфортно проводим наше время с пользой!"
            }
          </Text>
        </View>
        <View
          style={{
            background: "linear-gradient(to bottom, #090020, black)",
            flex: 1,
          }}
        >
          <Image
            style={{
              width: 600,
              height: 600,
              resizeMode: "stretch",
              marginHorizontal: "auto",
              marginVertical: "10%",
            }}
            source={{
              uri: meetAstro,
            }}
          />
          <Text
            style={{
              color: "white",
              fontSize: 30,
              fontFamily: "bold",
              textAlign: "center",
              margin: 20,
            }}
          >
            {
              "Наши ребята присутствовали на встрече\nс космонавтом-испытателем, героем России\nТокаревым Валерием Ивановичем"
            }
          </Text>
        </View>
        <View
          style={{
            background: "linear-gradient(to bottom, #090020, black)",
            flex: 1,
          }}
        >
          <Image
            style={{
              width: 600,
              height: 600,
              resizeMode: "stretch",
              marginHorizontal: "auto",
              marginVertical: "10%",
            }}
            source={{
              uri: "https://avatars.mds.yandex.net/i?id=8c39e2c2fd77fe18acc7dc788ce627670602f225-9846597-images-thumbs&n=13",
            }}
          />
          <Text
            style={{
              color: "white",
              fontSize: 30,
              fontFamily: "bold",
              textAlign: "center",
              margin: 20,
            }}
          >
            {
              "❞Астрономия побуждает душу\nвзглянуть ввысь и уводит\nНас из этого мира в другой❝\n©️ Конфуций"
            }
          </Text>
        </View>
      </View>
    </SafeAreaView>
  );
}
